<template>
  <div class="container flex-center">
    <card class="signin-container">
      <div class="flex-box padding-container">
        <div class="title flex-center">
          <div class="flex-box">
            <div class="app-name">inHeart</div>
            <div class="signup-text">로그인</div>
          </div>
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="email">이메일</label>
          <input
            class="custom-input"
            id="email"
            placeholder="예 : example@midasit.com"
            type="email"
            v-model="userEmail"
          >
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="pw">비밀번호</label>
          <input
            class="custom-input"
            id="pw"
            placeholder="비밀번호를 입력하세요"
            type="password"
            v-model="userPw"
          >
        </div>
        <div @click="submit" class="custom-button login-button">로그인</div>
        <div class="sign-up">
          <router-link to="sign-up">계정이 없으신가요?</router-link>
        </div>
      </div>
    </card>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {signIn} from '../api/user';

  export default {
    name: 'signin',
    components: {card},
    data() {
      return {
        userEmail: '',
        userPw: ''
      };
    },
    methods: {
      submit() {
        signIn({
          userEmail: this.userEmail,
          userPw: this.userPw
        })
          .then(res => {
            const response = res.data;
            switch (res.status) {
            case 200:
              this.$cookie.set('userName', response.data.userName, 1);
              this.$cookie.set('userEmail', response.data.userEmail, 1);
              this.$cookie.set('userImage', response.data.userImage, 1);
              this.$cookie.set('token', response.data.token, 1);
              this.$router.push('/');
              break;
            default:
              alert('로그인을 실패했습니다.');
            }
          })
          .catch(console.error);
      }
    }
  };
</script>

<style scoped>
  .login-button {
    margin-top: 15px;
    text-align: center;
  }

  .app-name {
    font-size: 2.5rem;
  }

  .signup-text {
    font-size: 24px;
    text-align: center;
  }

  .padding-container {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  h1 {
    font-size: 2rem;
  }

  input {
    font-family: 'SpoqaHanSans','Spoqa Han Sans', sans-serif;
  }

  .signin-container {
    height: auto;
  }

  .sign-up {
    margin: 10px 0;
    font-weight: 300;
    font-size: 18px;
  }

  .container {
    height: 100vh;
  }

  .custom-button {
    border: 2px solid #e1b52f;
    border-radius: 500px;
    padding: 10px;
    font-size: 1.5rem;
    cursor: pointer;
  }

  .custom-input {
    border: 0;
    border-bottom: 1px solid #6d6e72;
    background: none;
    font-size: 1rem;
    padding: 5px;
    width: 16rem;
    font-weight: 300;
  }

  .warn-label {
    color: red;
    margin-top: 4px;
    font-size: 0.85rem;
  }

  .input-item {
    width: 16rem;
    margin: 15px auto auto;
  }

  label {
    color: #212529;
    font-weight: 500;
    display: block;
    font-size: 18px;
  }
</style>
