import Vue from 'vue';
import Router from 'vue-router';
import index from '../pages/index.vue';
import meditation from '../pages/meditation.vue';
import mindCare from '../pages/mind-care.vue';
import mindStorage from '../pages/mind-storage.vue';
import signin from '../pages/signin.vue';
import signup from '../pages/signup.vue';
import player from '../components/player.vue';
import account from '../pages/account.vue';
import articleMeditation from '../pages/article-meditation.vue';
import articleView from '../pages/article-view.vue';
import articleEdit from '../pages/article-edit.vue';
import survey from '../pages/survey.vue';
import surveyView from '../pages/survey-view.vue';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {authRequired: true}
    },
    {
      path: '/meditation',
      name: 'meditation',
      component: meditation,
      meta: {authRequired: true}
    },
    {
      path: '/mind-care',
      name: 'mindCare',
      component: mindCare,
      meta: {authRequired: true}
    },
    {
      path: '/mind-storage',
      name: 'mindStorage',
      component: mindStorage,
      meta: {authRequired: true}
    },
    {
      path: '/sign-in',
      name: 'signin',
      component: signin,
    },
    {
      path: '/sign-up',
      name: 'signup',
      component: signup,
    },
    {
      path: '/player/:id',
      name: 'player',
      component: player,
      meta: {authRequired: true}
    },
    {
      path: '/account',
      name: 'account',
      component: account,
      meta: {authRequired: true}
    },
    {
      path: '/article-meditation/:id',
      name: 'article-meditation',
      component: articleMeditation,
      meta: {authRequired: true}
    },
    {
      path: '/article-view/:id',
      name: 'article-view',
      component: articleView,
      meta: {authRequired: true}
    },
    {
      path: '/article-edit',
      name: 'article-edit',
      component: articleEdit,
      meta: {authRequired: true}
    },
    {
      path: '/survey',
      name: 'survey',
      component: survey,
      meta: {authRequired: true}
    },
    {
      path: '/survey-view/:id',
      name: 'survey-view',
      component: surveyView,
      meta: {authRequired: true}
    },
  ],
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(routeInfo => routeInfo.meta.authRequired)) {
    if (Vue.cookie.get('token')) next();
    else next('/sign-in');
  } else next();
});
export default router;
