<template>
  <div class="container flex-center">
    <div>
      <card class="title-card">
        <div class="width100-container">
          <div class="flex-center">
            <h3>{{feelTitle}}</h3>
          </div>
          <div class="flex-center">{{dateConvert(date)}}, {{feelTime}}동안의 명상</div>
        </div>
      </card>
      <card class="auto-height">
        <article>{{feelText}}</article>
      </card>
      <floating-button @click="goBack" class="floating-button">
        <img alt="" src="../assets/icon/left-arrow.svg">
      </floating-button>
    </div>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import FloatingButton from '../components/inputs/floatingButton';
  import {getFeel} from '../api/feel';

  export default {
    name: 'article-meditation',
    components: {
      FloatingButton,
      card,
    },
    data() {
      return {
        feelTitle: '마음으로 하는 명상',
        contentsNo: 0,
        date: `${new Date().getFullYear()}년 1월 1일`,
        feelText: '',
        feelNo: this.$route.params['id'],
        feelTime:'',
      };
    },
    methods: {
      goBack() {
        window.history.go(-1);
      },
      dateConvert(date) {
        return `${date.slice(0, 4)}년 \
        ${parseInt(date.slice(5, 7))}월 \
        ${parseInt(date.slice(8, 10))}일`;
      },
    },
    async mounted() {
      const response = await getFeel({feelNo: this.feelNo}, this.$cookie.get('token'));
      if (response.status === 200) {
        const data = response.data.data[0];
        this.feelTitle = data.feelTitle;
        this.date = data.feelDate + data.feelTime;
        this.feelText = data.feelText;
        this.feelTime = `${data.feelTime.split(':')[0]<1?'':data.feelTime.split(':')[0]+'시간 '}${data.feelTime.split(':')[1]}분 ${data.feelTime.split(':')[2]}초`;
      } else {
        alert('존재하지 않습니다.');
        window.history.go(-1);
      }
    }
  }
</script>

<style scoped>
  article {
    max-height: calc(100vh - 200px);
    overflow-y: auto;
  }

  .width100-container {
    width: 100%;
  }

  .auto-height {
    height: auto;
  }

  .title-card {
    height: auto;
  }

  h3 {
    text-align: center;
  }

  hr {
    border: 3px solid #31a0e2;
    border-radius: 4px;
    margin: 10px 0;
  }

  .floating-button {
    bottom: 16px;
    right: 16px;
  }

  .floating-button img {
    padding: 7px 4px 4px 6px;
  }

  .title-card img {
    width: 24px;
    height: 24px;
    margin-left: 10px;
  }

  .container {
    height: 100vh;
  }
</style>
