<template>
  <div>
    <div class="select-container">
      <select v-model="favoriteType">
        <option value="1">머리 비우기</option>
        <option value="2">마음 비우기</option>
        <option value="3">머리 채우기</option>
        <option value="4">글 명상</option>
        <option value="5">자유 명상</option>
      </select>
    </div>
    <mediation-card
      :key="index"
      :meditation="data"
      v-for="(data,index) in list"/>
  </div>
</template>

<script>
  import MediationCard from '../cards/meditationCard';
  import {getStarContents} from '../../api/meditation';

  export default {
    name: 'favoriteMeditation',
    components: {MediationCard},
    watch: {
      async favoriteType(newValue) {
        this.getMeditations(parseInt(newValue));
      }
    },
    data() {
      return {
        favoriteType: 1,
        list: []
      }
    }, methods: {
      /**
       * 새로운 명상 목록을 가져오는 메소드
       * @params category 카테고리
       * @returns Array<Object>
       */
      async getMeditations(category) {
        const response = await getStarContents({categoryNo: category, token: this.$cookie.get('token')});
        if (response.status === 200) {
          this.list = response.data.data;
        }
        // this.list = [{contentsTitle: 'example' + category, contentsExplain: 'explanation', contentsIndex: '1', categoryNo: category}];
      },
    },
    mounted() {
      this.getMeditations(1);
    }
  }
</script>

<style scoped>
  .select-container {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
  }

  select {
    padding: 5px;
    border: none;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .3);
  }
</style>
