import axios from 'axios';
import {authorizationAxios} from './lib';

export const addFeel = ({contentsNo, feelTitle, feelText, feelTime, feelTypeNo}, token) => {
  return authorizationAxios(axios.post, '/feel', {
    contentsNo, feelTitle, feelText, feelTime, feelTypeNo
  }, token);
};

export const getFeel = ({feelNo}, token) => {
  return authorizationAxios(axios.get, '/feel', {feelNo}, token);
};

export const getFeelList = (token) => {
  return authorizationAxios(axios.get, '/feel/list', {}, token);
};


export const getFeelListByType = ({feelType}, token) => {
  return authorizationAxios(axios.get, '/feel', {feelType}, token);
};
