<template>
  <div>
    <card class="time-container flex-center">
      <div class="flex-center">
        <div class="input-number">
          <button @click="changeTime(0,1)" class="arrow flex-center"><img alt="up" src="../../assets/icon/up.svg"></button>
          <input id="hour" type="text" v-model="hour" value="00">
          <button @click="changeTime(0,-1)" class="arrow flex-center"><img alt="down" src="../../assets/icon/down.svg"></button>
        </div>
        <label for="hour">시간</label>
        <div class="input-number">
          <button @click="changeTime(1,1)" class="arrow flex-center"><img alt="up" src="../../assets/icon/up.svg"></button>
          <input id="minute" type="text" v-model="minute" value="00">
          <button @click="changeTime(1,-1)" class="arrow flex-center"><img alt="down" src="../../assets/icon/down.svg"></button>
        </div>
        <label for="minute">분</label>
        <div class="input-number">
          <button @click="changeTime(2,1)" class="arrow flex-center"><img alt="up" src="../../assets/icon/up.svg"></button>
          <input id="second" type="text" v-model="second" value="00">
          <button @click="changeTime(2,-1)" class="arrow flex-center"><img alt="down" src="../../assets/icon/down.svg"></button>
        </div>
        <label for="second">초</label>
      </div>
    </card>
    <meditation
      :key="index"
      :meditation="data"
      v-for="(data,index) in contents">
    </meditation>
  </div>
</template>

<script>
  import Meditation from '../cards/meditationCard';
  import Card from '../cards/card';
  import {getContents} from '../../api/meditation';

  const limit = [99, 59, 59];
  const type = ['hour', 'minute', 'second'];
  export default {
    name: "free-meditation",
    components: {
      Card,
      Meditation
    },
    data() {
      return {
        hour: '00',
        minute: '00',
        second: '00',
        contents: []
      }
    },
    methods: {
      changeTime(code, number) {
        let val = parseInt(this[type[code]]);
        if (!((val >= limit[code] && number === 1) || (val === 0 && number === -1))) {
          this[type[code]] = this.numberString(val + number);
        }

      },
      numberString(number) {
        number = parseInt(number);
        if (number >= 10) return number;
        else return `0${number}`;
      }
    },
    async mounted() {
      try {
        const response = await getContents({categoryNo: 5}, this.$cookie.get('token'));
        if (response.status === 200) {
          this.contents = response.data.data;
        }
      } catch (e) {
        console.log(e);
        alert('데이터 로드에 실패했습니다.');
        window.location.reload();
      }
    }
  }
</script>

<style scoped>
  .arrow {
    width: 3rem;
    padding: 5px;
  }

  .arrow > img {
    height: 16px;
    width: 16px;
  }

  label {
    text-align: center;
  }

  input, label {
    text-shadow: 2px 2px 3px rgba(0, 0, 0, .2);
  }

  .time-container .flex-center {
    width: 100%;
  }

  .input-number {
  }

  input {
    /*height: 3rem;*/
    padding: 5px;
    border-bottom: 1px solid #2C3E50;
    border-top: 1px solid #2C3E50;
    font-size: 1.5rem;
    text-align: center;
    background: none;
    width: 3rem;
    font-weight: 100;
  }

  .time-container {
    height: 120px !important;
  }

  .time-container label {
    padding: 5px;
    font-size: 1.2rem;
  }
</style>
