<template>
  <div class="container">
    <navigation
      :categories="categories"
      :item_style="{fontSize:'0.8rem',marginLeft:'7px',width:'25%',display:'flex',justifyContent:'center',alignItems:'center',wordBreak:'keep-all'}"
      @change="onChangeCategory"
    ></navigation>
    <meditation-card
      :key="i"
      v-for="(item, i) in content"
      :meditation="item"
      class="meditation-card"/>
    <bottom-navigation></bottom-navigation>
  </div>
</template>

<script>
  import bottomNavigation from '../components/navigations/bottomNavigation';
  import meditationCard from '../components/cards/meditationCard';
  import navigation from '../components/navigations/topNavigation';
  import {getContents} from '../api/meditation';

  export default {
    name: "meditation",
    components: {
      bottomNavigation,
      meditationCard,
      'navigation': navigation
    },
    data() {
      return {
        categories: [
          {name: '머리 비우기', color: '#E1B52F'},
          {name: '마음 비우기', color: '#E76577'},
          {name: '머리 채우기', color: '#A55AA2'},
          {name: '글 명상', color: '#31a0e2'},
        ],
        content: [
          // {contentsTitle: '제목', contentsExplain: '설명', contentsIndex: '1', contentsNo: 1, categoryNo: 1}
        ],
      };
    },
    mounted() {
      this.getContentList(1);
    },
    methods: {
      onChangeCategory(index) {
        this.getContentList(index + 1);

      },
      async getContentList(categoryNo) {
        const response = await getContents({categoryNo}, this.$cookie.get('token'));
        this.content = response.data;
        if (response.status === 200) {
          this.content = response.data.data;
        }
      }
    }
  }
</script>

<style scoped>
  .container {
    height: calc(100vh - 56px);
    margin-bottom: 56px;
    overflow: auto;
    padding: 10px 10px 15px 10px;
  }
</style>
