<template>
  <div class="container flex-center">
    <div>
      <card class="title-card">
        <div class="width100-container">
          <div class="flex-center">
            <h3>{{meditation.contentsTitle}}</h3>
          </div>
          <div class="flex-center">{{meditation.contentsExplan}}</div>
        </div>
      </card>
      <card class="auto-height">
        <article v-html="article"></article>
      </card>
      <custom-button @click="finish" class="finish-button">명상 완료</custom-button>
      <floating-button @click="goBack" class="floating-button">
        <img alt="" src="../assets/icon/left-arrow.svg">
      </floating-button>
    </div>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import FloatingButton from '../components/inputs/floatingButton';
  import {getContent} from '../api/meditation';
  import axios from 'axios';
  import CustomButton from '../components/inputs/customButton';

  export default {
    name: 'article-meditation',
    components: {
      CustomButton,
      FloatingButton,
      card,
    },
    data() {
      return {
        meditation: {},
        article: '',
        startTime: 0,
      };
    },
    methods: {
      goBack() {
        window.history.go(-1);
      },
      finish() {
        this.$router.push({
          path: '/article-edit', query: {
            id: this.meditation.contentsNo,
            time: Math.floor((new Date().getTime() - this.startTime) / 1000),
            type: 2
          }
        });
      },
    },
    async mounted() {
      const response = await getContent({contentsNo: this.$route.params['id']}, this.$cookie.get('token'));
      if (response.status === 200) {
        this.meditation = response.data.data[0];
        this.article = (await axios.get('http://midasit.jaeheon.kr/meditation/text/마음을%20다스리는%20명상의%20글.txt')).data.replace(/\n/g, '<br>');
        this.startTime = new Date().getTime();
      } else {
        alert('존재하지 않습니다.');
        window.history.go(-1);
      }
    }
  }
</script>

<style scoped>
  .finish-button {
    font-size: 1.5rem;
    margin: 20px auto;
    background: #eee;
    width: 400px;
    max-width: calc(100vw - 60px);
  }

  article {
    max-height: calc(100vh - 230px);
    overflow-y: auto;
  }

  .width100-container {
    width: 100%;
  }

  .auto-height {
    height: auto;
  }

  .title-card {
    height: auto;
  }

  h3 {
    text-align: center;
  }

  hr {
    border: 3px solid #31a0e2;
    border-radius: 4px;
    margin: 10px 0;
  }

  .floating-button {
    bottom: 16px;
    right: 16px;
  }

  .floating-button img {
    padding: 7px 4px 4px 6px;
  }

  .title-card img {
    width: 24px;
    height: 24px;
    margin-left: 10px;
  }

  .container {
    height: 100vh;
  }
</style>
