<template>
  <div class="container flex-center">
    <div>
      <card id="top-card">
        <div id="top-card-container">
          <div class="flex-center">
            <input id="feel-title" type="text" v-model="feelTitle">
          </div>
          <div id="date-text">2019년 3월 12일</div>
        </div>
      </card>
      <card class="edit-card">
        <div class="card-container">
          <div class="textarea-container">
            <textarea v-model="feelText"></textarea>
          </div>
          <div class="flex-center button-container">
            <button @click="save" class="custom-button">저장하기</button>
            <button @click="$router.go(-1)" class="custom-button">작성 취소</button>
          </div>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {addFeel} from '../api/feel';

  export default {
    name: 'article-meditation',
    components: {
      card,
    },
    data() {
      return {
        feelTitle: '',
        feelText: '',
      };
    }, methods: {
      async save() {
        const time = Math.floor(this.$route.query['time']) + 1;
        const zero = num => num < 10 ? '0' + num : num;
        const timeString = `${zero(Math.floor(time / 3600))}:${zero(Math.floor(time / 60))}:${zero(Math.floor(time % 60))}`;
        const response = await addFeel({
          contentsNo: parseInt(this.$route.query['id']),
          feelTitle: this.feelTitle,
          feelText: this.feelText,
          feelTime: timeString,
          feelTypeNo: parseInt(this.$route.query['type']),
        }, this.$cookie.get('token'));
        if (response.status === 201) {
          this.$router.push('/');
        } else {
          alert('저장에 실패했습니다.');
        }
      }
    }, mounted() {
      if (!(this.$route.query['id'] && this.$route.query['time'] && this.$route.query['type'])) {
        alert('잘못된 접근입니다.');
        this.$router.push('/');
      }
    }
  }
</script>

<style scoped>
  #feel-title {
    background: none;
    border-bottom: 1px solid #444;
    font-size: 1rem;
    padding: 5px;
    margin: 5px;
    width: 15rem;
    text-align: center;
  }

  #top-card-container {
    width: 100%;
  }

  .button-container {
    margin-top: 10px
  }

  .edit-card {
    height: auto;
  }

  .textarea-container {
    height: calc(100vh - 250px);
  }

  textarea {
    height: calc(100vh - 260px);
    width: 350px;
    max-width: calc(100vw - 100px);
    background: none;
    font-size: 1rem;
  }

  #date-text {
    text-align: center;
    padding: 5px;
    width: 100%;
  }

  #top-card {
    height: 100px;
    display: block;
  }

  .custom-button {
    border: 2px solid #E1B52F;
    border-radius: 7rem;
    padding: 10px;
    font-size: 1.2rem;
    width: 7rem;
  }

  .custom-button:nth-child(2) {
    margin-left: 20px;
  }

  input {
    outline: 0
  }

  h1, h3 {
    text-align: center;
  }

  hr {
    border: 3px solid #31a0e2;
    border-radius: 4px;
    margin: 10px 0;
  }

  textarea {
    border: 0;
    resize: none;
    font-family: 'SpoqaHanSans', 'Spoqa Han Sans',sans-serif;
    font-weight: 300;
  }

  .container {
    height: 100vh;
  }
</style>
