<template>
  <div>
    <div :key="survey.surveyNo" class="survey-card-container" v-for="survey in displayContents">
      <router-link :to="`survey-view/${survey.surveyNo}`">
        <card class="survey-card">
          <span>{{dateConvert(survey.surveyDate)}} 설문</span>
        </card>
      </router-link>
    </div>
    <router-link to="survey">
      <floating-button>
        <img alt="edit" src="../../assets/icon/edit.svg">
      </floating-button>
    </router-link>
  </div>
</template>

<script>
  import FloatingButton from '../inputs/floatingButton';
  import {getSurveyList} from '../../api/survey';
  import Card from '../cards/card';

  export default {
    name: 'mind-check',
    components: {Card, FloatingButton},
    data() {
      return {
        displayContents: [],
      }
    },
    methods: {
      dateConvert(date) {
        return `${date.slice(0, 4)}년 \
        ${parseInt(date.slice(5, 7))}월 \
        ${parseInt(date.slice(8, 10))}일`;
      },
    },
    async mounted() {
      const response = await getSurveyList(this.$cookie.get('token'));
      if (response.status === 200) {
        this.displayContents = response.data.data;
      }
    }
  }
</script>

<style scoped>
  .survey-card-container {
    margin-top: 20px;
  }

  .survey-card {
    height: 100px;
    color: #3c4043;
  }

  select {
    padding: 5px;
    border: none;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .3);
  }

</style>
