<template>
  <label class="switch">
    <input type="checkbox">
    <span class="slider round"></span>
  </label>
</template>

<script>
  export default {
    name: "custom-switch"
  }
</script>

<style scoped>

  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    border-radius: 100px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }

  input:checked + .slider {
    background-color: #2C3E50;
  }

  input:checked + .slider:before {
    transform: translateX(26px);
  }

  .slider:before {
    border-radius: 50%;
  }

</style>
