<template>
  <div>
    <div class="container">
      <navigation :categories="categories" :item_style="{width:'50%'}" @change="onChangeCategory"></navigation>
      <mind-container v-if="selectedCategory === 0"/>
      <mind-check v-if="selectedCategory === 1"/>
    </div>
    <bottom-navigation></bottom-navigation>
  </div>
</template>
<script>
  import bottomNavigation from '../components/navigations/bottomNavigation';
  import navigation from '../components/navigations/topNavigation';
  import MindContainer from '../components/tabs/mind-container';
  import MindCheck from '../components/tabs/mind-check';

  export default {
    name: 'mind-storage',
    components: {
      MindCheck,
      MindContainer,
      navigation,
      bottomNavigation,
    },
    data() {
      return {
        categories: [
          {name: '마음 담기', color: '#2C3E50'},
          {name: '마음 점검', color: '#2C3E50'}
        ],
        selectedCategory: 0,
      }
    },
    methods: {
      onChangeCategory(index) {
        if (index > 0) {
          this.selectedCategory = 1;
        } else {
          this.selectedCategory = 0;
        }
      }
    }
  }
</script>

<style scoped>
  .container {
    height: calc(100vh - 56px);
    margin-bottom: 56px;
    overflow: auto;
    padding: 10px 10px 15px 10px;
  }
</style>
