import axios from 'axios';
import {authorizationAxios} from './lib';

export const writeSurvey = (data, token) => {
  return authorizationAxios(axios.post, '/survey', data, token);
};
export const getSurveyList = (token) => {
  return authorizationAxios(axios.get, '/survey/list', {}, token);
};

export const getSurveyTitleList = (token) => {
  return authorizationAxios(axios.get, '/survey/title/list', {}, token);
};


export const getSurvey = ({surveyNo}, token) => {
  return authorizationAxios(axios.get, '/survey', {surveyNo}, token);
};
