<template>
  <div class="container flex-center">
    <card class="signup-container">
      <div class="flex-box padding-container">
        <div class="title flex-center">
          <div class="flex-box">
            <div class="app-name">inHeart</div>
            <div class="signup-text">회원가입</div>
          </div>
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="reg-name">이름</label>
          <input
            class="custom-input"
            id="reg-name"
            name="name"
            placeholder="예: 홍길동"
            type="text"
            v-model="userName"
          >
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="reg-email">이메일</label>
          <input
            class="custom-input"
            id="reg-email"
            placeholder="예 : example@midasit.com"
            type="email"
            name="email"
            v-model="userEmail"
          >
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="reg-pw">비밀번호</label>
          <input
            class="custom-input"
            id="reg-pw"
            placeholder="비밀번호를 입력하세요"
            type="password"
            name="password"
            v-model="userPw"
          >
        </div>
        <div class="input-item">
          <label class="custom-label login-label" for="reg-re-pw">비밀번호 확인</label>
          <input
            class="custom-input"
            id="reg-re-pw"
            placeholder="비밀번호를 다시 입력하세요."
            type="password"
            v-model="userPwChk"
          >
        </div>
        <div class="input-item file-container">
          <label class="custom-label login-label" for="reg-picture">프로필 사진</label>
          <label
            class="custom-label login-label file-selector"
            for="reg-picture"
            id="picture-text"
          >{{imageFileName}}</label>
          <input @input="uploadImage" class="custom-input" id="reg-picture" type="file">
        </div>
        <div
          @click="submit"
          class="custom-button register-button"
        >회원 가입
        </div>
        <div class="sign-in">
          <router-link to="sign-in">계정이 이미 있습니다.</router-link>
        </div>
      </div>
    </card>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {signUp} from '../api/user';

  const filterExtension = ['png', 'gif', 'jpg', 'jpeg', 'PNG', 'GIF', 'JPG', 'JPEG'];
  export default {
    name: 'signup',
    components: {card},
    data() {
      return {
        userEmail: '',
        userName: '',
        userPw: '',
        userPwChk: '',
        userImage: '',
        imageFileName: '사진 업로드'
      };
    },
    methods: {
      uploadImage(e) {
        let img = e.target.files[0];
        // if (img && filterExtension.includes(img.name.substr(-5).split('.')[1])) {
        this.imageFileName = img.name;
        this.userImage = img;
        // } else {
        //   alert('해당 파일은 지원되지 않는 파일입니다.');
        // }
      },
      submit() {
        if (this.userPw !== this.userPwChk) {
          alert('비밀번호와 비밀번호 확인이 다릅니다.');
          return;
        }
        let fd = new FormData();
        fd.append('userEmail', this.userEmail);
        fd.append('userName', this.userName);
        fd.append('userPw', this.userPw);
        fd.append('userImage', this.userImage);
        signUp(fd)
          .then(res => {
            alert('성공');
            this.$router.push('/sign-in');
          })
          .catch(e => {
            alert('실패');
          });
      }
    },
    mounted() {
    }
  };
</script>

<style scoped>
  .sign-in {
    margin: 10px 0;
    font-weight: 300;
    font-size: 16px;
  }
  .register-button {
    margin-top: 15px;
    text-align: center;
  }

  .app-name {
    font-size: 2.5rem;
  }

  .signup-text {
    font-size: 24px;
    text-align: center;
  }

  .padding-container {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  h1 {
    font-size: 2rem;
  }

  input {
    font-family: 'SpoqaHanSans','Spoqa Han Sans', sans-serif;
  }

  .signup-container {
    height: auto;
  }

  .custom-button {
    border: 2px solid #E1B52F;
    border-radius: 500px;
    padding: 10px;
    font-size: 1.2rem;
    width: 16rem;
  }

  .custom-button:nth-child(2) {
    margin-left: 20px;
  }

  .container {
    height: 100vh;
  }

  .file-selector {
    display: inline-block;
    padding: 5px;
    font-size: inherit;
    line-height: normal;
    vertical-align: middle;
    cursor: pointer;
    border-bottom: 1px solid #6d6e72;
    width: 16rem;
  }

  .file-container input[type="file"] {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
  }

  .custom-button {
    border: 2px solid #e1b52f;
    border-radius: 50px;
    padding: 10px;
    font-size: 1.5rem;
    user-select: none;
    cursor: pointer;
  }

  .custom-input {
    border: 0;
    border-bottom: 1px solid #6d6e72;
    background: none;
    font-size: 17px;
    font-weight: 300;
    padding: 5px;
    width: 16rem;
  }

  .warn-label {
    color: red;
    margin-top: 4px;
    font-size: 0.85rem;
  }

  .input-item {
    width: 16rem;
    margin: 15px auto auto;
  }

  label {
    color: #212529;
    font-weight: 500;
    display: block;
    font-size: 18px;
  }
</style>
