<template>
  <div class="container flex-center">
    <div>
      <card class="date-card">
        <div>2019년 3월 12일</div>
      </card>
      <card class="auto-height">
        <div class="survey-container">
          <div class="survey" v-for="(question,index) in questions">
            <span>{{index+1}}. {{question}}</span><br>
            <select v-model="result[index]">
              <option value="5">매우 만족</option>
              <option value="4">대체로 만족</option>
              <option value="3">보통</option>
              <option value="2">대체로 불만족</option>
              <option value="1">매우 불만족</option>
            </select>
          </div>
          <div class="flex-center">
            <button @click="save" class="custom-button">저장하기</button>
            <button @click="goBack" class="custom-button">작성 취소</button>
          </div>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {writeSurvey} from '../api/survey';

  export default {
    name: 'survey',
    components: {
      card,
    }, data() {
      return {
        questions: [
          "오늘 명상은 만족스러우셨나요?",
          "명상 전의 기분은 어떠셨나요?",
          "지금은 어떠신가요?",
          "편안함을 느끼고 계신가요?",
          "부정적인 감정을 느끼고 계신가요?",
          "객관적인 나를 보고 계신가요?",
          "타인과 좋은 감정을 공유하고 싶으신가요?",
          "오늘 명상은 만족스러우셨나요?"
        ],
        result: [5, 5, 5, 5, 5, 5, 5, 5]
      };
    }, methods: {
      async save() {
        const data = {};
        this.result.forEach((result, index) => {
          data[`survey_${index + 1}`] = parseInt(result);
        });
        const response = await writeSurvey(data, this.$cookie.get('token'));
        if (response.status === 200) {
          this.$router.push('/mind-storage');
        }
      },
      goBack() {
        window.history.go(-1);
      }
    }
  }
</script>

<style scoped>
  select {
    font-size: 1rem;
    width: 100%;
    margin-top: 5px;
    background: none;
  }

  .survey {
    margin-bottom: 15px;
  }

  .survey-container {
    width: 100%;
    padding: 10px;

  }

  .auto-height {
    height: auto;
  }

  .date-card {
    height: 70px;
    text-align: center;
  }

  h1, h3 {
    text-align: center;
  }

  hr {
    border: 3px solid #31a0e2;
    border-radius: 4px;
    margin: 10px 0;
  }

  .custom-button {
    border: 2px solid #E1B52F;
    border-radius: 7rem;
    padding: 10px;
    font-size: 1.2rem;
    width: 7rem;
  }

  .custom-button:nth-child(2) {
    margin-left: 20px;
  }

  .container {
    padding-top: 10px;
    padding-bottom: 10px;
  }
</style>
