<template>
  <div>
    <div class="container">
      <card class="hello-card">
        <div class="hello flex-center">안녕하세요, {{userName}}님</div>
        <img @click="$router.push('account')" src="../assets/icon/account.svg">
      </card>
      <card class="hello-card">자주하는 질문</card>
      <card :key="content.faqNo" v-for="content in contents">
        <div class="faq-container">
          <div class="faq-question"><span class="prefix">Q. </span> {{content.faqQuestion}}</div>
          <div class="faq-answer"><span class="prefix">A. </span> {{content.faqAnswer}}</div>
        </div>
      </card>
    </div>
    <bottom-navigation></bottom-navigation>
  </div>
</template>

<script>
  import bottomNavigation from '../components/navigations/bottomNavigation';
  import card from '../components/cards/card';
  import navigation from '../components/navigations/topNavigation';
  import player from '../components/player';
  import {getFAQList} from '../api/faq';

  export default {
    name: "index",
    components: {
      bottomNavigation,
      card,
      navigation,
      player
    }, data() {
      return {
        categories: [
          {name: '머리 비우기', color: '#E1B52F'},
          {name: '마음 비우기', color: '#E76577'},
          {name: '마음 채우기', color: '#A55AA2'},
          {name: '글 명상', color: '#31a0e2'},
        ],
        userName: '',
        contents: '',
      };
    },
    methods: {},
    async mounted() {
      this.userName = this.$cookie.get('userName');
      const response = await getFAQList(this.$cookie.get('token'));
      if (response.status === 200) {
        this.contents = response.data.data;
      }
    }
  }
</script>

<style scoped>
  .hello {
    width: calc(100% - 36px);
    padding-left: 40px;
  }

  .button-img {
    width: 1rem;
    height: 1rem;
  }

  .prefix {
    font-size: 20px;
  }

  .faq-container {
    width: 100%;
  }

  .faq-question {
    margin-bottom: 10px;
  }

  .hello-card {
    height: 70px;
  }

  .container {
    height: calc(100vh - 56px);
    margin-bottom: 56px;
    overflow: auto;
    padding: 10px 10px 15px 10px;
  }

  .hello-card img {
    height: 36px;
    width: 36px;
    border-radius: 50%;
    padding: 5px;
    cursor: pointer;
  }
</style>
