<template>
  <div class="height100vh-container">
    <div class="container">
      <navigation
        :categories="categories"
        :item_style="top_navigation_item_style"
        @change="changeTab"
      />
      <favorite-meditation class="tab" v-if="view===0"/>
      <free-meditation class="tab" v-else-if="view===1"/>
      <meditation-alarm class="tab" v-else-if="view===2"/>
    </div>
    <bottom-navigation></bottom-navigation>
  </div>
</template>

<script>
  import bottomNavigation from '../components/navigations/bottomNavigation';
  import card from '../components/cards/card';
  import navigation from '../components/navigations/topNavigation';
  import meditationCard from '../components/cards/meditationCard';
  import freeMeditation from '../components/tabs/freeMeditation';
  import MeditationAlarm from '../components/tabs/meditationAlarm';
  import FavoriteMeditation from '../components/tabs/favoriteMeditation';

  export default {
    name: 'mind-care',
    components: {
      FavoriteMeditation,
      meditationCard,
      MeditationAlarm,
      bottomNavigation,
      card,
      navigation,
      freeMeditation,
    }, data() {
      return {
        top_navigation_item_style: {width: '33%'},
        sub_navigation_item_style: {width: '25%', fontSize: '0.85rem', padding: '2px 2px', marginLeft: '5px'},
        sub_navigation_layout_style: {height: '24px'},
        view: 0,
        category: 0,
        categories: [
          {name: '즐겨찾기', color: '#2C3E50'},
          {name: '자유 명상', color: '#2C3E50'},
          {name: '명상 알람', color: '#2C3E50'},
        ], categories2: [
          {name: '머리 비우기', color: '#E1B52F'},
          {name: '마음 비우기', color: '#E76577'},
          {name: '머리 채우기', color: '#A55AA2'},
          {name: '글 명상', color: '#31a0e2'},
        ],
      };
    }, methods: {
      /**
       * It changes category of favorite meditation.
       */
      changeCategory(index) {
        this.category = index;
      },
      changeTab(index) {
        this.view = index;
      }
    }
  }
</script>

<style scoped>
  .height100vh-container{
    height: 100vh;
  }
  .tab {
    margin-top: 20px;
  }

  .container {
    height: calc(100vh - 56px);
    margin-bottom: 56px;
    overflow: auto;
    padding: 10px 10px 15px 10px;
  }
</style>
