<template>
  <div class="container flex-center">
    <card class="account-card">
      <div class="account-container">
        <h1>사용자 정보</h1>
        <div class="profile-image">
          <img :src="`http://midasit.jaeheon.kr/pImg/${$cookie.get('userImage')}`" alt="">
        </div>
        <h3>{{name}} 님</h3>
        <h3>{{email}}</h3>
        <h2 class="count-text">명상 횟수</h2>
        <div class="analysis">
          <div class="row">
            <div class="col"><img alt="" src="../assets/icon/play-yellow.svg"></div>
            <div class="col"><img alt="" src="../assets/icon/play-red.svg"></div>
            <div class="col"><img alt="" src="../assets/icon/play-pink.svg"></div>
            <div class="col"><img alt="" src="../assets/icon/play-blue.svg"></div>
          </div>
          <div class="row">
            <div class="col" v-for="data in analysis">{{data}}</div>
          </div>
        </div>
        <div class="flex-center">
          <button @click="logout" class="custom-button">로그아웃</button>
          <button @click="exit" class="custom-button">회원탈퇴</button>
        </div>
      </div>
    </card>
    <floating-button @click="goBack" class="floating-button">
      <img alt="" src="../assets/icon/left-arrow.svg">
    </floating-button>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {exit, getMeditationInfo, signOut} from '../api/user';
  import CustomButton from '../components/inputs/customButton';
  import FloatingButton from '../components/inputs/floatingButton';

  export default {
    name: 'account',
    components: {
      FloatingButton,
      CustomButton,
      card
    }, data() {
      return {
        name: '',
        email: '',
        analysis: []
      };
    }, mounted() {
      if (!!this.$cookie.get('userName')) {
        this.setUserInfo();
      }
    },
    methods: {
      async setUserInfo() {
        this.name = this.$cookie.get('userName');
        this.email = this.$cookie.get('userEmail');
        const response = await getMeditationInfo(this.$cookie.get('token'));
        this.analysis = Object
          .keys(response.data.data[0])
          .filter(key => key.includes('category'))
          .map(key => response.data.data[0][key]);
      },
      async logout() {
        this.$cookie.delete('token');
        this.$cookie.delete('userName');
        this.$cookie.delete('userEmail');
        this.$router.push('sign-in');
        const response = await signOut(this.$cookie.get('token'));
      },
      async exit() {
        const response = await exit(this.$cookie.get('token'));
        if (response.status === 200) {
          this.$cookie.delete('token');
          this.$cookie.delete('userName');
          this.$cookie.delete('userEmail');
          alert('성공적으로 탈퇴되었습니다.');
          this.$router.push('sign-up');
        }
      },
      goBack() {
        window.history.go(-1);
      }
    }
  }
</script>

<style scoped>
  .account-card {
    height: auto;
  }

  .account-container {
    padding-bottom: 10px;
    width: inherit;
    overflow: auto;
  }

  .count-text {
    margin-top: 10px;
  }

  .buttons {
    width: min-content;
  }

  h1, h2, h3 {
    text-align: center;
  }

  .custom-button {
    border: 2px solid #e1b52f;
    border-radius: 500px;
    padding: 10px;
    font-size: 1.2rem;
    width: 7rem;
    font-weight: 500;
  }

  .custom-button:nth-child(2) {
    margin-left: 20px;
  }

  .analysis {
    margin: 10px 0;
  }

  .analysis img {
    width: 32px;
    height: 32px;
  }

  .container {
    height: 100vh;
  }

  .profile-image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
  }

  .profile-image img {
    border-radius: 100px;
    width: 100px;
    height: 100px;
  }

  .row {
    display: flex;
    justify-content: space-around;
    align-items: center
  }

  .floating-button {
    bottom: 16px;
    right: 16px;
  }

  .floating-button img {
    padding: 7px 4px 4px 6px;
  }

</style>
