import axios from 'axios';
import {authorizationAxios} from './lib';

const baseUrl = 'http://midasit.jaeheon.kr';

export const signIn = ({userEmail, userPw}) => {
  return axios.post(`${baseUrl}/user/login`, {
    userEmail,
    userPw
  });
};

export const signUp = formData => {
  return axios.post(`${baseUrl}/user/signup`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
};

export const getMeditationInfo = (token) => {
  return authorizationAxios(axios.get, '/user/meditotal', {}, token);
};

export const signOut = (token) => {
  return authorizationAxios(axios.get, '/user/logout', {}, token);
};


export const exit = (token) => {
  return authorizationAxios(axios.delete, '/user/exit', {}, token);
};
