<template>
  <div class="container flex-center">
    <div>
      <card class="date-card">
        <div>2019년 3월 12일</div>
      </card>
      <card class="auto-height">
        <div class="survey-container">
          <div class="survey" v-for="(question,index) in questions">
            <span>{{index+1}}. {{question.surveyTitle}}</span><br>
            <div class="disabled">{{question.surveyValue}}</div>
          </div>
        </div>
      </card>
      <floating-button @click="goBack" class="floating-button">
        <img alt="" src="../assets/icon/left-arrow.svg">
      </floating-button>
    </div>
  </div>
</template>

<script>
  import card from '../components/cards/card';
  import {getSurvey} from '../api/survey';
  import FloatingButton from '../components/inputs/floatingButton';

  export default {
    name: 'survey-view',
    components: {
      FloatingButton,
      card,
    }, data() {
      return {
        surveyNo: this.$route.params['id'],
        questions: [
          {"surveyTitle": "오늘 명상은 만족스러우셨나요?", "surveyValue": "."},
          {"surveyTitle": "명상 전의 기분은 어떠셨나요?", "surveyValue": "."},
          {"surveyTitle": "지금은 어떠신가요?", "surveyValue": "."},
          {"surveyTitle": "편안함을 느끼고 계신가요?", "surveyValue": "."},
          {"surveyTitle": "부정적인 감정을 느끼고 계신가요?", "surveyValue": "."},
          {"surveyTitle": "객관적인 나를 보고 계신가요?", "surveyValue": "."},
          {"surveyTitle": "타인과 좋은 감정을 공유하고 싶으신가요?", "surveyValue": "."},
          {"surveyTitle": "오늘 명상은 만족스러우셨나요?", "surveyValue": "."}
        ]
      };
    },
    methods: {
      goBack() {
        window.history.go(-1);
      }
    },
    async mounted() {
      const response = await getSurvey({surveyNo: this.surveyNo}, this.$cookie.get('token'));
      if (response.status === 200) {
        this.questions = response.data.data.map(item => {
          if (item.surveyValue === 1) item.surveyValue = '매우 불만족';
          else if (item.surveyValue === 2) item.surveyValue = '대체로 불만족';
          else if (item.surveyValue === 3) item.surveyValue = '보통';
          else if (item.surveyValue === 4) item.surveyValue = '대체로 만족';
          else if (item.surveyValue === 5) item.surveyValue = '매우 만족';
          return item;
        });
      }
    }
  }
</script>

<style scoped>
  .survey {
    margin-bottom: 15px;
  }

  .survey-container {
    width: 100%;
    padding: 10px;

  }

  .auto-height {
    height: auto;
  }

  .date-card {
    height: 70px;
    text-align: center;
  }

  h1, h3 {
    text-align: center;
  }

  hr {
    border: 3px solid #31a0e2;
    border-radius: 4px;
    margin: 10px 0;
  }

  .disabled {
    background: #ccc;
    margin-top: 5px;
    padding: 5px;
    border-radius: 5px;
  }

  .floating-button {
    bottom: 16px;
    right: 16px;
  }

  .floating-button img {
    padding: 7px 4px 4px 6px;
  }

  .title-card img {
    width: 24px;
    height: 24px;
    margin-left: 10px;
  }

  .container {
    padding-top: 10px;
    padding-bottom: 10px;
  }
</style>
