import axios from 'axios';

const baseUrl = 'http://midasit.jaeheon.kr';

/**
 *
 * @param method {Function} axios.get, axios.post 등
 * @param url {string}경로
 * @param data {Object} 데이터
 * @param token {string} JWT Token
 * @return any {Promise}
 */
export const authorizationAxios = (method, url, data, token) => {
  if (method === axios.get || method === axios.delete) {
    return method(`${baseUrl}${url}`, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `JWT ${token}`,
      },
      params: data,
    });
  } else {
    return method(`${baseUrl}${url}`, data, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `JWT ${token}`
      }
    });
  }
};
