<template>
  <div class="item mini-item">
    <div class="inner-container">
      <div class="flex-box mini-flex-container">
        <div>
          <div class="title"><h3>{{meditation.contentsTitle}}</h3></div>
          <div class="description-mini"><span>{{meditation.contentsExplain}}</span></div>
        </div>
        <div class="flex-center">
          <router-link :to="moveURL">
            <div class="icon32">
              <img :src="getURL" alt="play">
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getPlayerIconColor} from '../../lib/icon';

  export default {
    name: 'mediationCard',
    props: {
      meditation: Object
    }, computed: {
      getURL() {
        return getPlayerIconColor(this.meditation.categoryNo ? this.meditation.categoryNo : 0);
      },
      moveURL() {
        if (this.meditation.categoryNo === 4) return `/article-meditation/${this.meditation.contentsNo}`;
        else return `/player/${this.meditation.contentsNo}`;
      }
    }
  }
</script>

<style scoped>
  .item {
    padding: 10px;
    background: #eee;
    height: 200px;
    margin: 20px auto 0;
    width: 400px;
    max-width: calc(100vw - 60px);
    align-items: center;
    justify-content: center;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .2);
    transition: all .4s ease-in-out;
    display: flex;
  }

  .item:nth-child(1) {
    margin-top: 0;
  }

  .mini-item {
    height: 100px;
  }

  .mini-flex-container {
    justify-content: space-between;
    display: flex;
    align-items: center;
    height: 100%;
  }

  .icon32 {
    width: 32px;
    height: 32px;
    border-radius: 32px;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .2);
  }

  .icon32 > img {
    width: 32px;
    height: 32px;
  }

  .inner-container {
    box-shadow: inset 0 5px 10px 0 rgba(0, 0, 0, .15);
    height: 100%;
    width: 100%;
    padding: 10px 10px 5px;
    border-bottom: 1px solid rgba(0, 0, 0, .05);
    align-items: center;
  }

  .title > h1 {
    color: #6D6E72;
    font-size: 2.5rem;
    font-weight: 100;
    text-align: center;
  }

  .description > span {
    text-align: center;
  }

  .description-mini {
    font-size: 1rem;
  }
</style>
