<template>
  <div class="flex-center player-container">
    <div class="container">
      <div class="player-nav">
        <button @click="goBack" class="top-button"><img alt="" src="../assets/icon/left-arrow.svg"></button>
        <button @click="changeStar" class="top-button"><img :src="getStarIcon" alt=""></button>
      </div>
      <div class="middle-flex-center">
        <div class="margin-bottom56">
          <div class="image">
            <img :style="getRotate" alt="album cover" src="../assets/album-cover.jpg">
          </div>
          <div class="title"><h2>{{contentsTitle}}</h2></div>
          <div class="sub-title"><h3>{{contentsExplan}}</h3></div>
          <div class="player">
            <div class="start-time">{{getCurrentTime}}</div>
            <div class="time-bar-container">
              <div :style="getTimeBar" class="time-bar"></div>
            </div>
            <div class="end-time">{{getTimeString(endTime)}}</div>
          </div>
          <div class="control">
            <button @click="play()">
              <img alt="play" src="../assets/icon/play-yellow.svg">
            </button>
            <button @click="pause()">
              <img alt="pause" src="../assets/icon/pause-yellow.svg">
            </button>
            <button @click="stop()" class="stop-btn">
              <img alt="stop" src="../assets/icon/stop.svg">
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import {deleteStarContent, getContent, getStarContent, setStarContent} from '../api/meditation';

  export default {
    name: "player",
    data() {
      return {
        contentsTitle: '',
        contentsExplan: '',
        audio: null,
        currentTime: 0,
        endTime: 0,
        isStarred: false,
        id: this.$route.params['id'],
      }
    },
    computed: {
      getStarIcon() {
        return this.isStarred ? '/icon/fill-star.svg' : '/icon/star.svg';
      },
      getCurrentTime() {
        return this.getTimeString(this.currentTime);
      },
      getTimeBar() {
        return {
          width: `${this.currentTime / (this.endTime / 100)}%`,
        }
      },
      getRotate() {
        return {
          transform: `rotate(${this.currentTime * 5}deg)`,
        };
      }
    },
    methods: {
      getNumberString(n) {
        if (n < 1) {
          return '00'
        } else if (n < 10) {
          return '0' + n;
        } else {
          return n
        }
      },
      getTimeString(second) {
        second = parseInt(second);
        return `${this.getNumberString(parseInt(second / 60))}:${this.getNumberString(second % 60)}`;
      },
      play() {
        this.audio.play().then(() => {
        }).catch(err => console.error(err));

      },
      pause() {
        this.audio.pause();
      },
      stop() {
        this.audio.pause();
        this.audio.currentTime = 0;

      },
      async changeStar() {
        if (this.isStarred) {
          const response = await deleteStarContent({contentsNo: this.id, token: this.$cookie.get('token')});
          if (response.status === 200) {
            this.isStarred = false;
          }
        } else {

          const response = await setStarContent({contentsNo: this.id, token: this.$cookie.get('token')});
          if (response.status === 201) {
            this.isStarred = true;
          }
        }
      },
      goBack() {
        this.pause();
        this.$router.go(-1);
      }
    },
    async mounted() {
      const response = await getContent({contentsNo: this.id}, this.$cookie.get('token'));
      if (response.status === 200) {
        if (response.data.data[0].contentsType !== 'sound') {
          this.$router.go(-1);
          return;
        }
        this.contentsExplan = response.data.data[0].contentsExplan;
        this.contentsTitle = response.data.data[0].contentsTitle;
        this.type = response.data.data[0].categoryNo === 5 ? 1 : 2;
        const audio = new Audio(`http://midasit.jaeheon.kr/meditation/sound/${response.data.data[0].contentsFile}`);
        const vue = this;
        getStarContent({contentsNo: this.id, token: this.$cookie.get('token')})
          .then(response => {
            if (response.status === 200) {
              this.isStarred = true;
            } else if (response.status === 204) {
              this.isStarred = false;
            }
          });
        setTimeout(() => {
          try {
            vue.audio = audio;
            vue.endTime = audio.duration;
            vue.currentTime = parseInt(vue.audio ? vue.audio.currentTime : -1);
            const timer = setInterval(() => {
              this.currentTime = this.audio.currentTime.toFixed(2)
            }, 16);
            audio.onended = () => {
              clearInterval(timer);
              vue.$router.push({path: '/article-edit', query: {id: vue.id, time: vue.endTime, type: vue.type}});
            };
          } catch (e) {
            console.error(e);
            alert('잘못된 접근입니다. 이전으로 돌아갑니다.');
            vue.$router.go(-1);
          }
        }, 500);
      }
    }
  }
</script>

<style scoped>
  /*
    레이아웃 조정을 위한 class
   */
  .margin-bottom56 {
    margin-bottom: 56px;
  }

  .player-container {
    padding: 10px;
  }

  .container {
    height: 100%;
  }

  .player-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .top-button {
    background: #E1B52F;
    height: 48px;
    width: 48px;
    border-radius: 48px;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .3);
  }

  .top-button img {
    padding-top: 2px;
    height: 36px;
    width: 36px;
    transition: opacity 1s ease-in-out;
  }

  .title {
    margin-bottom: 5px;
  }

  .title, .sub-title {
    text-align: center;
  }

  .sub-title {
    margin-bottom: 10px;
  }

  .control button:nth-child(2), .control button:nth-child(3) {
    margin-left: 10px;
  }

  .control {
    margin-top: 10px;
    display: flex;
    justify-content: center;
  }

  .stop-btn {
    background: #E1B52F;
  }

  .control button {
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .15);

  }

  .control button, .control img {
    border-radius: 64px;
    height: 64px;
    width: 64px;
  }

  .control img {
    height: 64px;
    width: 64px;
  }

  .flex-center {
    background: white;
    height: 100vh;
  }

  .image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
  }

  .middle-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 48px - 20px)
  }

  .image > img {
    height: 60%;
    width: 60%;
    border-radius: 60%;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .15);
  }

  .player {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  .player > div {
    margin-left: 10px;
  }

  .time-bar-container {
    width: 100%;
    height: 5px;
    background: #DBDDDB;
  }

  .time-bar {
    width: 33px;
    height: 5px;
    background: #E1B52F;
  }

  .stop-btn img {
    margin-top: 3px;
    border-radius: 0;
    height: 28px;
    width: 28px;
  }
</style>
