import axios from 'axios';
import {authorizationAxios} from './lib';

export const getContent = ({contentsNo}, token) => {
  return authorizationAxios(axios.get, `/contents`, {
    contentsNo,
  }, token);
};

export const getContents = ({categoryNo}, token) => {
  return authorizationAxios(axios.get, '/contents/category/list', {
    categoryNo
  }, token);
};

export const getContentsWithStar = ({categoryNo, token}) => {
  return authorizationAxios(axios.get, '/contents/star/list', {
    categoryNo
  }, token);
};

export const setStarContent = ({contentsNo, token}) => {
  return authorizationAxios(axios.post, '/star', {contentsNo}, token);
};

export const deleteStarContent = ({contentsNo, token}) => {
  return authorizationAxios(axios.delete, '/star', {contentsNo}, token);
};

export const getStarContent = ({contentsNo, token}) => {
  return authorizationAxios(axios.get, '/star', {contentsNo}, token);
};

export const getStarContents = ({categoryNo, token}) => {
  return authorizationAxios(axios.get, '/star/list', {categoryNo}, token);
};
