<template>
  <div class="tab">
    <div class="select-container">
      <select v-model="feelType">
        <option value="0">전체</option>
        <option value="1">자유 기록</option>
        <option value="2">명상 소감</option>
      </select>
    </div>
    <div :key="i.feelNo" class="feel-card-container" v-for="i in displayContents">
      <router-link :to="`article-view/${i.feelNo}`">
        <card class="feel-card">
          <div class="width100-container">
            <span>{{i.feelTitle}}</span><br>
            <div class="space-between">
              <span>{{dateConvert(i.feelDate)}}</span>
              <span>{{getFeelTypeText(i.feelTypeNo)}}</span>
            </div>
          </div>
        </card>
      </router-link>
    </div>
    <router-link to="article-edit">
      <floating-button>
        <img alt="edit" src="../../assets/icon/edit.svg">
      </floating-button>
    </router-link>
  </div>
</template>

<script>
  import FloatingButton from '../inputs/floatingButton';
  import {getFeelList, getFeelListByType} from '../../api/feel';
  import Card from '../cards/card';

  export default {
    name: 'mind-container',
    components: {Card, FloatingButton},
    data() {
      return {
        feelType: 0,
        contents: [],
        displayContents: [],
      };
    },
    watch: {
      feelType(value) {
        if (value > 0) this.displayContents = this.contents.filter(val => val.feelTypeNo.toString() === value);
        else this.displayContents = this.contents;
      }
    },
    methods: {
      getFeelTypeText(feelTypeNo) {
        if (feelTypeNo === 1) return '자유 기록';
        else if (feelTypeNo === 2) return '명상 소감';
        else return '-';
      },

      dateConvert(date) {
        return `${date.slice(0, 4)}년 \
        ${parseInt(date.slice(5, 7))}월 \
        ${parseInt(date.slice(8, 10))}일`;
      },
    },
    async mounted() {
      const {feelType} = this;
      let response = {};
      if (feelType === 0) response = await getFeelList(this.$cookie.get('token'));
      else response = await getFeelListByType({feelType: feelType}, this.$cookie.get('token'));
      this.contents = response.data.data;
      this.displayContents = this.contents;
    }
  }
</script>

<style scoped>
  .tab {
    margin-top: 20px;
  }

  .feel-card-container {
    margin-top: 20px;
  }

  .feel-card {
    height: 100px;
    color: #3c4043;
  }

  .space-between {
    display: flex;
    justify-content: space-between;
  }

  .width100-container {
    width: 100%
  }

  .select-container {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
  }

  select {
    padding: 5px;
    border: none;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .3);
  }

</style>
